.MintingPage{
    background: linear-gradient(to bottom, #ffb31ade, white);
    width:393px;
    height: 2000px;
    margin: 0 auto; /*393x2000px이 노트북 화면 가운데 출력됩니다!!*/
    display: flex;
    flex-direction: column;
    align-items: center; /* 요소들 수직 가운데 정렬*/
    font-family: "Noto Sans KR";
  }
  
  /*대학가 멤버십 서비스*/
  .Min{
    margin-top: 30px;
    margin-left: -150px;
    font-weight: 800;
    color: #353332;
    text-shadow: -0.8px 0px #F0f0f0, 0px 0.8px #F0f0f0, 0.8px 0px #F0f0f0, 0px -0.8px #F0f0f0; /*글씨 테두리*/
  }
  span{ /*설명 중 강조하는 부분*/
    color:#FF6636;
  }
  
  
  /*NFT 이미지들*/
  .nft1{
    transform: translate(60%, 20%);
  }
  .nft2{
    transform: translate(50%, 80%);
  }
  .nft4{
    transform: translate(30%, 60%);
  }
  .nft5{
    transform: translate(120%, 50%);
  } 
  
  /*드롭다운창*/
  .input {
    width: 250px;
    padding: 13px;
    font-size: 16px;
    font-weight: bold;
    border-radius: 20px;
    border:none;
  }
  
  .school-list {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    max-height: 160px;
    overflow-y: auto;
    width: 250px;
    margin: 1px;
    background-color: white;
    border-radius: 20px;
  }
  
  
  .school-item {
    font-weight: bold;
    padding: 10px;
    cursor: pointer;
  }
  
  
  
  
  /*랜덤NFT 이미지*/
  .randomNFT{
    margin-top: 80px;  
    filter: blur(5px); /*이미지 블러처리*/
    -webkit-filter: blur(5px);
  } 
  

  
  /*민팅하기 버튼*/
  .MintingButton{
    width: 110px; 
    height: 35px;
    border: none; 
    border-radius: 70px;
    background-color: #D9D9D9;
    color:black;
    font-weight: bold;
    font-size: 13px;
    margin-top: -380px;
  }
  
  .MintingButton:active{
    border: 1px solid #838383;
  }
