  .nfts {
    opacity: 0; 
    transform: translateX(-100%); 
    transition: opacity 4s ease, transform 4s ease;
  }
  
  .randomNFT {
    opacity: 0; 
    transform: translateX(100%); /* 초기 위치를 오른쪽으로 이동 */
    transition: opacity 3s ease, transform 3s ease;
  }
  
  .NFT.visible,
  .randomNFT.visible {
    opacity: 1; /* 나타날 때 투명도 조절 */
    transform: translateX(0); /* 화면 중앙으로 이동 */
  }
  

.NFT img,
.Min {
  opacity: 0; /* 초기에 숨김 */
  transform: translateX(-100%); /* 초기 위치를 왼쪽으로 이동 */
  transition: opacity 3s ease, transform 3s ease;
}

.NFT.visible img,
.Min.visible {
  opacity: 1; /* 나타날 때 투명도 조절 */
  transform: translateX(0); /* 화면 중앙으로 이동 */
}

.HamburgerImage {
    opacity: 0; 
    transition: opacity 3s ease, transform 3s ease;
  }
  
  .HamburgerImage.visible,
  .nfts.visible {
    opacity: 1; 

  }
  

  .nfts.visible {
    opacity: 1; 
    transform: translateY(0); 
  }
  